<template>
  <div>
    <div>
      <headpage :islogin="islogin"></headpage>
      <div class="height100">
        <div class=" arrowsbg">
          <div class=" flex flex-col items-center m-auto py-16 pt-30 max-w-3xl leading-18">
            <h1 class=" text-center text-white text-3xl md:text-6xl md:leading-tight mb-6">{{$t('bannerTitle1')}}<br />{{$t('bannerTitle2')}}</h1>
            <p class=" text-center text-yellow-300 text-xl md:text-2xl mb-16 px-8">{{$t('bannerContent')}}</p>
            <a class="inline-flex items-center justify-center font-mono uppercase transition ease-in duration-200 hover:no-underline active:shadow-inner focus:outline-none rounded-sm h-12 px-8 py-3 text-sm border text-white shadow hover:shadow-hover hover:border-transparent active:border-transparent bg-primary border-black hover:bg-gray-700 active:bg-black mb-4"
               @click="toDashboard">{{$t('bannerButton')}}</a>
            <p class=" text-sm text-white">{{$t('contactTit')}} <a class=" text-white underline hover:text-primary" href="mailto:contact@apron.network">{{$t('contact')}}</a></p>

          </div>
        </div>


      </div>
      <footpage></footpage>
    </div>


  </div>
</template>

<script>
import Footpage from '@components/footpage';
import Headpage from '@components/headpage';
import api from '@/api';

export default {
  name: 'index',
  components: {
    Footpage,
    Headpage,
  },
  data() {
    return {
      islogin: false,
    };
  },
  methods: {
    toDashboard() {
      if (this.islogin) {
        this.$router.push('/dashboard');
      } else {
        this.$router.push('/loginRegister/login');
      }
    },
  },
  mounted() {
    api.loginApi.islogin().then((result) => {
      if (result && result.success) {
        this.islogin = result.msg;
      }
    });
  },
};
</script>

