import request from './request';


const get_projects = async () => {
  const data = await request.post('/api/auth/get_projects');
  return data;
};

const create_project = async (params) => {
  const data = await request.post('/api/auth/create_project', params);
  return data;
};

const delete_project = async (params) => {
  const data = await request.post('/api/auth/delete_project', params);
  return data;
};

const change_project = async (params) => {
  const data = await request.post('/api/auth/change_project', params);
  return data;
};

export default {
  get_projects,
  create_project,
  change_project,
  delete_project,
};
