import request from './request';


const register = async (params) => {
  const data = await request.post('/api/auth/register', params);
  return data;
};

const login = async (params) => {
  const data = await request.post('/api/auth/login', params);
  return data;
};

const loginout = async () => {
  const data = await request.get('/api/auth/logout');
  return data;
};

const forgot = async (params) => {
  const data = await request.get('/api/auth/forget_password', params);
  return data;
};

const resetpassword = async (params) => {
  const data = await request.post('/api/auth/reset_password', params);
  return data;
};

const islogin = async () => {
  const data = await request.get('/api/auth/islogin');
  return data;
};

const resetnewpassword = async (params) => {
  const obj = {
    new_password: params.new_password,
    password_verify: params.password_verify,
  };
  const data = await request.post(`/api/auth/reset_password_confirm/${params.token}`, obj);
  return data;
};

export default {
  islogin,
  resetpassword,
  resetnewpassword,
  register,
  login,
  loginout,
  forgot,
};
