<script>
import mixin from '@mixin';

export default {
  name: 'headpage',
  mixins: [mixin],
  props: ['islogin'],
  data() {
    return {
      isActive: true,
      langType: null,
    };
  },
  methods: {
    ToggleNav() {
      this.isActive = !this.isActive;
    },
    toDashboard() {
      if (this.islogin) {
        this.$router.push('/dashboard');
      } else {
        this.$router.push('/loginRegister/login');
      }
    },
    changeLaguages(type) {
      this.$i18n.locale = type;
      this.langType = type;
      localStorage.setItem('lang', type);
    },
  },
  mounted() {
    this.langType = this.$i18n.locale;
  },
};
</script>

