import vue from 'vue';
import VueRouter from 'vue-router';
import index from '@views/index';

vue.use(VueRouter);

export default new VueRouter({
  routes: [
    {
      path: '/',
      name: 'home',
      component: index,
    },
    {
      path: '/resetPassword/:token',
      name: 'resetPassword',
      component: () => import('@components/login/resetPassword'),
    },
    {
      path: '/loginRegister',
      name: 'loginRegister',
      component: () => import('@views/loginRegister'),
      children: [
        {
          path: 'login',
          component: () => import('@components/login/login'),
        },
        {
          path: 'register',
          component: () => import('@components/login/register'),
        },
        {
          path: 'forgot',
          component: () => import('@components/login/forgot'),
        },
      ],
    },
    {
      path: '/policy',
      name: 'policy',
      component: () => import('@views/policy'),
    },
    {
      path: '/terms',
      name: 'Terms',
      component: () => import('@views/terms'),
    },
    {
      path: '/dashboard',
      name: 'dashboard',
      component: () => import('@views/dashboard'),
      redirect: '/dashboard/welcome',
      children: [
        {
          path: 'welcome',
          component: () => import('@components/dashboard/welcome'),
        },
        {
          path: 'detail',
          component: () => import('@components/ethereum/detail'),
          redirect: '/detail/prosetting',
          children: [
            {
              path: 'request',
              name: 'request',
              component: () => import('@components/ethereum/request'),
            },
            {
              path: 'prosetting',
              name: 'prosetting',
              component: () => import('@components/ethereum/prosetting'),
            },
          ],
        },
        {
          path: 'heco',
          component: () => import('@components/ethereum/project'),
        },
        {
          path: 'settings',
          component: () => import('@components/settings/settings'),
          redirect: '/dashboard/settings/account',
          children: [
            {
              path: 'account',
              component: () => import('@components/settings/account'),
            },
            {
              path: 'billing',
              component: () => import('@components/settings/billing'),
            },
          ],
        },

      ],
    },

  ],

});
